<template>
    <div class="home-wrap">
        <div class="user" v-if="userInfo">
          <img :src="userInfo.head_portrait" class="avatar">
          <div class="info">
            <div>
              <span class="name">{{userInfo.realname}}</span>
              <img :src="userInfo.meal_level | markImg" alt="" class="mark">
            </div>

            <div class="center">
              <span class="phone"><i class="iconfont">&#xe65e;</i>{{userInfo.phone}}</span>
              <span class="time">{{userInfo.last_login_time | formatDate}}</span>
            </div>

            <div class="btn_wrap">
              <span class="public_btn green"><i class="iconfont">&#xe66a;</i></span>
              <span class="public_btn blue"><i class="iconfont">&#xe652;</i></span>
            </div>
          </div>
          
          <!-- <div class="pannel" v-if="">
            <span class="title">A1 <i class="iconfont">&#xe615;</i></span>
            <span class="btn"></span>
            <p class="time">2019-01-23</p>
          </div> -->
        </div>


        <div class="content">
          <div class="ercharts_wrap">
            <div class="public_title"><span>7</span></div>
            <line-charts/> 
          </div>
          <ul class="lists">
            <li class="item" v-for="(item,index) in lists">
              <i class="iconfont" v-html="item.code" :style="{color: item.color}"></i>
              <div class="fr">
                <p class="title">{{item.title}}</p>
                <p class="num" :style="{color: item.color}">{{item.num}}</p>                
              </div>

            </li>
          </ul>
        </div>
    </div>
</template>

<script>
import { getIndex } from "@/api/public";
import lineCharts from "@/components/charts/lineCharts";

export default {
  data() {
    return {
      userInfo: {}, 
      count_num: {},
      lists: []
    };
  },
  components: { lineCharts },
  created() {
    getIndex().then(res => {
      if (res.data.code == 1) {
        this.userInfo = res.data.data.agent_info;

        this.count_num = res.data.data.count_num;

        this.lists = [
          {
            title: "",
            num: this.count_num.is_today,
            color: "#40C9C6",
            code: "&#xe64f;"
          },
          {
            title: "",
            num: this.count_num.all,
            color: "#F44864",
            code: "&#xe667;"
          },
          {
            title: "",
            num: this.count_num.browse,
            color: "#FF6E5C",
            code: "&#xe658;"
          },
          {
            title: "",
            num: 0,
            color: "#36A3F7",
            code: "&#xe668;"
          },
          {
            title: "",
            num: this.count_num.updown,
            color: "#34BFA3",
            code: "&#xe659;"
          },
          {
            title: "",
            num: this.count_num.lower,
            color: "#F5C239",
            code: "&#xe657;"
          }
        ];
      }
    });
  },
  methods: { 

   
  },
  mounted() {}
};
</script>

<style lang='scss' scoped>
.home-wrap {
  width: 100%;
}
.user {
  position: relative;
  height: 120px;
  padding: 18px 0;
  background: #fff;
  box-shadow: 0px 4px 6px 0px rgba(201, 211, 223, 1);
  .avatar {
    display: inline-block;
    vertical-align: middle;
    width: 80px;
    height: 80px;
    margin: 0 20px;
    border-radius: 50%;
  }
  .info {
    vertical-align: middle;
    display: inline-block;
    .center {
      margin: 6px 0;
    }
    .btn_wrap .public_btn {
      margin-right: 20px;
    }
    .name {
      vertical-align: middle;
      font-size: 18px;
      font-weight: bold;
      color: rgba(44, 62, 80, 1);
    }
    .mark {
      vertical-align: middle;
      margin-left: 30px;
      width: 90px;
      height: 22px;
    }
    .phone {
      margin-right: 30px;
      i {
        margin-right: 15px;
      }
      font-weight: 500;
      color: rgba(44, 62, 80, 1);
    }
    .time {
      font-weight: 500;
      color: rgba(67, 89, 110, 1);
    }
  }
  .pannel {
    position: absolute;
    right: 20px;
    top: 10px;
    width: 270px;
    padding: 20px;
    height: 100px;
    color: #fff;
    background: rgba(234, 206, 152, 1);
    box-shadow: 0px 2px 4px 0px rgba(201, 211, 223, 1);
    border-radius: 5px;
    .title {
      padding: 6px;
      background: rgba(214, 176, 115, 1);
      i {
        margin-left: 4px;
        cursor: pointer;
      }
    }
    .btn {
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translateY(-50%);
      width: 58px;
      height: 26px;
      line-height: 26px;
      text-align: center;
      color: #82581f;
      border: 1px solid rgba(130, 88, 31, 1);
      border-radius: 13px;
      cursor: pointer;
    }
    .time {
      margin-top: 28px;
    }
  }
}
.content {
  display: flex;
  margin-top: 20px;
  .ercharts_wrap {
    flex: 1;
    margin-right: 20px;
    background: #fff;
    height: 500px;
    padding: 20px 0 0 20px;
    #chart {
      height: 400px;
      width: 100%;
    }
  }
  .lists {
    width: 350px;
    .item {
      background: #fff;
      width: 100%;
      height: 124px;
      padding: 0 36px;
      .iconfont {
        float: left;
        font-size: 44px;
        margin-top: 40px;
      }
      .fr {
        width: 46%;
        text-align: center;
      }
      .title {
        margin-top: 20px;
        margin-bottom: 20px;
        height: 20px;
        font-size: 20px;
        color: rgba(44, 62, 80, 1);
      }
      .num {
        height: 36px;
        font-size: 48px;
        font-weight: bold;
      }
    }
    .item + .item {
      margin-top: 10px;
    }
  }
}
</style>



