<template>
    <div class="chart" id="chart"></div>
</template>

<script>
import { getIndex } from "@/api/public";
var echarts = require("echarts/lib/echarts");
// 
require("echarts/lib/chart/line");
// 
require("echarts/lib/component/tooltip");
require("echarts/lib/component/title");
// legend
require("echarts/lib/component/legend");

export default {
  data() {
    return {
      esfData: [],
      rentData: [],
      shopData: [],
      date: [],
      filters: [],
      filtersdata: [],
      chart: null
    };
  },
  created() {},
  methods: {
    async fetchData() {
      await getIndex().then(res => {
        if (res.data.code == 1) {
          for (let i = 6; i >= 0; i--) {
            this.date.push(this.handleDate(i)); 
          }
          this.resetFilters()
          this.rentData = this.handleData(res.data.data.table.rent);
          this.esfData = this.handleData(res.data.data.table.sale);
          this.shopData = this.handleData(res.data.data.table.shop); 
        }
      });
    },
    handleDate(n, type) {
      var d = new Date();
      var year = d.getFullYear();
      var mon = d.getMonth() + 1;
      var day = d.getDate();
      var s;
      var r;
      if (day <= n) {
        if (mon > 1) {
          mon = mon - 1;
        } else {
          year = year - 1;
          mon = 12;
        }
      }
      d.setDate(d.getDate() - n);
      year = d.getFullYear();
      mon = d.getMonth() + 1;
      day = d.getDate();

      s =
        year +
        "" +
        (mon < 10 ? "0" + mon : mon) +
        "" +
        (day < 10 ? "0" + day : day) +
        "";

      r = day;
      if (type) {
        return r;
      } else {
        return s;
      }
    },
    handleData(data) {
      let a = [];

      data.forEach(element => {
        let i = this.filtersdata.indexOf(element.day);
        if (i > -1) { 
          this.filters[i].hits = element.hits;
        }
      });
      this.filters.forEach(item => {
        a.push(item.hits)
      }) 

      this.resetFilters()
      return a;
    },

    resetFilters() {
      this.filters = []
      this.filtersdata = []
      for (let i = 6; i >= 0; i--) {
        this.filters.push({ hits: 0, day: this.handleDate(i, 1) });
        this.filtersdata.push(this.handleDate(i, 1));
      }
    },
    initChart() {
      var chart = echarts.init(document.getElementById("chart"));
      chart.setOption({
        xAxis: {
          data: this.date,
          boundaryGap: false,
          axisTick: {
            show: false
          }
        },
        // 
        grid: {
          left: 30,
          right: 50,
          bottom: 20,
          top: 40,
          containLabel: true
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          },
          padding: [5, 10]
        },
        yAxis: {
          axisTick: {
            show: false
          }
        },
        legend: {
          left: "right",
          data: ["", "", ""]
        },
        series: [
          {
            name: "",
            itemStyle: {
              normal: {
                color: "#FF005A",
                lineStyle: {
                  color: "#FF005A",
                  width: 2
                }
              }
            },
            // smooth: true,
            type: "line",
            data: this.shopData,
            animationDuration: 2800,
            animationEasing: "cubicInOut"
          },
          {
            name: "",
            // smooth: true,
            type: "line",
            itemStyle: {
              normal: {
                color: "#3888fa",
                lineStyle: {
                  color: "#3888fa",
                  width: 2
                },
                areaStyle: {
                  color: "#f3f8ff"
                }
              }
            },
            data: this.esfData,
            animationDuration: 2800,
            animationEasing: "quadraticOut"
          },
          {
            name: "",
            // smooth: true,
            type: "line",
            itemStyle: {
              normal: {
                color: "#34BFA3",
                lineStyle: {
                  color: "#34BFA3",
                  width: 2
                },
                areaStyle: {
                  color: "#f3f8ff"
                }
              }
            },
            data: this.rentData,
            animationDuration: 2800,
            animationEasing: "quadraticOut"
          }
        ]
      });
    }
  },
  mounted() {
    this.fetchData().then(res => {
      this.initChart();
    });
  }
};
</script>



